import { useEffect, useState } from 'react';
import { DoctorsProfileHeader } from './components/profile-header';
import { DoctorsProfileSideBar } from './components/profile-sidebar';

const DoctorsProfile = () => {

    const [doctorsData, setDoctorsData] = useState('');
    const [onlineAvailability, setOnlineAvailability] = useState(false);
    const [appointments] = useState([
        { 'name': 'First Patient', 'date': '2022-02-12', 'event': 'Clinicals', 'time': '12:00' },
        { 'name': 'Second Patient', 'date': '2022-02-13', 'event': 'Test', 'time': '14:00' },
        { 'name': 'Third Patient', 'date': '2022-02-13', 'event': 'Test', 'time': '15:00' },
    ]); //Sample data for now...

    useEffect(() => {
        //Import the data for now...
        setDoctorsData(JSON.parse(sessionStorage.getItem("doctor")));

        //eslint-disable-next-line
    }, [])

    return (
        <>
            <div className="background">
                <DoctorsProfileHeader
                    doctorsData={doctorsData}
                />
                <DoctorsProfileSideBar
                    appointments={appointments}
                />
                <div className="plain-container d-none">

                    {/* Appointment availability */}
                    <div className='box-container'>
                        <div className="doc-profile-header">
                            <h2>2</h2>
                        </div>
                        <h3>Availability</h3>
                        <div className="questions">
                            <div className="question mb-1">
                                <p style={{ color: '#888' }}>Select your availability</p>
                                <div className='radio-group'>
                                    <div className="radio-button">
                                        <label>
                                            <input
                                                type='radio'
                                                className='custom-radio'
                                                name='availability'
                                                checked={onlineAvailability}
                                                onChange={() => setOnlineAvailability(true)}
                                            />Online
                                        </label>
                                    </div>
                                    <div className="radio-button">
                                        <label>
                                            <input
                                                type='radio'
                                                className='custom-radio'
                                                name='availability'
                                                checked={onlineAvailability}
                                                onChange={() => setOnlineAvailability(true)}
                                            />Offline
                                        </label>
                                    </div>
                                    <div className="radio-button">
                                        <label>
                                            <input
                                                type='radio'
                                                className='custom-radio'
                                                name='availability'
                                                checked={onlineAvailability}
                                                onChange={() => setOnlineAvailability(true)}
                                            />Both
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </>
    )
}

export default DoctorsProfile;
